.user__card {
    display: grid;
    padding: 10px 20px;
    margin: 10px;
    background-color: rgba(97, 250, 166, 0.1);
    grid-template-areas:
        'foto name location'
        'btn status location';
    grid-template-columns: 2fr 5fr 2fr;
}

.user__foto {
    grid-area: foto;
    width: 70px;
    height: 70px;
    object-fit: cover;
    border-radius: 5px;
    margin-bottom: 5px;
}

